@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap");
html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Roboto", sans-serif;
  background-color: #f2f2f2;
  line-height: 1.5;
}

h1, h2, p {
  margin: 0;
}

.container {
  max-width: 85%;
}
@media (min-width: 1200px) {
  .container {
    max-width: 1100px;
  }
}

.header {
  width: 100%;
  height: 350px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  row-gap: 2rem;
}
@media (min-width: 768px) {
  .header {
    height: 400px;
    flex-direction: row;
    column-gap: 2rem;
  }
}
@media (min-width: 1024px) {
  .header {
    height: 425px;
    column-gap: 4rem;
  }
}

.header__image {
  width: 280px;
  object-fit: cover;
}
@media (min-width: 768px) {
  .header__image {
    width: 350px;
  }
}
@media (min-width: 1024px) {
  .header__image {
    width: 420px;
  }
}

.header__title {
  margin: 0;
  font-size: 4rem;
  font-weight: 100;
  color: #3d3d3d;
}
@media (min-width: 768px) {
  .header__title {
    font-size: 4.5rem;
  }
}
@media (min-width: 1024px) {
  .header__title {
    font-size: 5.6rem;
  }
}
.header__title span {
  font-weight: 700;
  position: relative;
}
.header__title span::after {
  content: "";
  width: 100%;
  height: 15px;
  background-color: #f0da50;
  position: absolute;
  inset-inline-start: 5px;
  inset-block-end: 12px;
  z-index: -1;
}

.main {
  margin: 7rem auto;
}

.main__projects {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
}
@media (min-width: 768px) {
  .main__projects {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1024px) {
  .main__projects {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}

.card__project {
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  background-color: #ffffff;
  box-shadow: 1px 1px 5px -2px rgba(0, 0, 0, 0.4);
}

.card__projectImage {
  width: 100%;
  height: 220px;
  object-fit: cover;
  border-radius: 10px 10px 20px 20px;
}

.card__projectInfo {
  padding: 1rem;
  border-radius: 10px;
}

.card__projectTitle {
  font-size: 2.5rem;
  font-weight: 700;
}

.card__projectDescription {
  margin-block: 0.8rem 1rem;
  height: 60px;
  font-size: 1.4rem;
}

.card__projectLinks {
  display: flex;
  justify-content: flex-end;
  column-gap: 1rem;
}

.card__projectLink {
  padding: 0.5rem;
  display: flex;
  align-items: center;
  column-gap: 0.5rem;
  border-radius: 0.5rem;
  border: 1px solid rgba(0, 0, 0, 0.2);
}
@media (min-width: 1024px) {
  .card__projectLink:hover {
    background-color: #f0da50;
    border: 1px solid #f0da50;
    transition: 0.3s;
  }
}

.card__projectLinkUrl {
  text-decoration: none;
  color: #000000;
  font-size: 1.4rem;
}

.card__projectLinkIcon {
  width: 18px;
}

.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 65px;
}

.footer__content {
  font-size: 1.4rem;
  font-weight: 400;
}
.footer__content a {
  color: #000000;
}